import React from 'react';
import PropTypes from 'prop-types';
import * as classes from './items.module.scss';

const TodoItem = ({todo}) => {
    return (
        <div>
            <div className={classes.container}>
                <div> {todo.task} </div>
            </div>
        </div>
    );
};

TodoItem.propTypes = {
    todo: PropTypes.object
};
TodoItem.defaultProps = {
    todo: {}
};

export default TodoItem;
